body {
  font-family: 'Roboto Slab', serif;
  color: #808080;
}

.app {
  margin: 2rem;
  display: grid;
}

.pages_title {
  margin-bottom: 20px;
  margin-top: 40px;
  text-align: center;
  font-size: 2em;
  font-weight: 900;
}

.contact_wrapper {
  text-align: center;
  font-size: 18px;
  font-weight: 400;
  line-height: 10px;
}

.clients_lists {
  margin-left: 20px;
  font-weight: 400;
}

.clients_lists a {
  text-decoration: none;
  color: #808080;
}

.clients_lists a:hover {
  font-size: 1.1em;
  color: #651fff;
  font-weight: 900;
}

.bold {
  font-weight: 900;
}

.nav-logo {
  display: flex;
  margin-bottom: 40px;
}

.nav-logo__img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.nav-wrapper {
  display: flex;
  font-size: 16px;
  justify-content: space-between;
  padding: 30px;
  cursor: pointer;
  color: #26bfd4;
  transition: 0.5 ease-in-out;
}

.nav-wrapper:hover {
  color: #207b88;
}

.nav-wrapper .nav-link-wrapper {
  height: 22px;
  border-bottom: 1px solid transparent;
  transition: 0.5s ease-in-out;
  margin-right: 20px;
  text-transform: uppercase;
  font-size: 0.9rem;
}

.nav-wrapper .nav-link-wrapper a {
  color: #8a8a8a;
  text-decoration: none;
}

.nav-wrapper .nav-link-wrapper a:hover {
  color: #651fff;
}

.nav-wrapper .nav-link-wrapper:hover {
  border-bottom: 1px solid #651fff;
  font-weight: 900;
}

.nav-wrapper .nav-link-wrapper .nav-link-active {
  border-bottom: 2px solid black;
  color: black;
}

.nav-line {
  grid-column: 1/-1;
  grid-row: line-s/line-e;
  border-top: 1px solid #ccc;
}

.footer-wrapper {
  margin-top: 30px;
  background: #eee;
  padding: 10px 10px;
  text-align: center;
}

.footer-wrapper-container {
  margin: 1rem;
  display: block;
  column-gap: 50px;
}

.footer_socials {
  width: 30px;
  display: inline-block;
  transition: transform 250ms;
  margin-left: 35px;
  margin-right: 35px;
}

a._icons:hover {
  transform: translate(-2px);
}

.contact-form-wrapper {
  margin: 40px;
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr;
  color: #313131;
}

.contact-form-wrapper input {
  font-family: 'Roboto Slab', serif;
  color: #313131;
  padding: 5px 0px;
  width: 100%;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid #DEDEDE;
  background-color: transparent;
  transition: 0.5s all ease-in-out;
  font-size: 0,5em;
  margin-bottom: 21px;
}

.contact-form-wrapper input:focus {
  outline: none;
  border-bottom: 1px solid #DEDEDE;
}

textarea {
  font-family: 'Roboto Slab', serif;
  border: 1px solid #DEDEDE;
  background-color: transparent;
  height: 100px;
  font-size: 0,1rem;
  outline: none;
  padding: 15px 20px;
}

.select-element {
  font-family: 'Roboto Slab', serif;
  padding-left: 20px;
  border: 1px solid #ccc;
  font-size: 0.8rem;
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  background-color: transparent;
  outline: none;
}

.button_wrapper {
  text-align: center;
  margin-bottom: 24px;
}

.btn {
  font-family: 'Roboto Slab', serif;
  text-transform: uppercase;
  cursor: pointer;
  width: 80px;
  height: 40px;
  font-size: 1em;
  font-weight: 500;
  border: 1px solid transparent;
  border-radius: 5px;
  align-items: center;
  transition: all 0.5s ease-in-out;
  background-color: black;
  color: white;
}

.btn:active, .btn:focus {
  outline: none;
}

.btn:hover {
  background-color: #207b88;
}

.one-column {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr;
}

.two-column {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr 1fr;
}

.three-column {
  display: grid;
  grid-gap: 21px;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 40px auto;
}

.right-side {
  display: block;
  margin-left: 20px;
}

.left-side {
  display: block;
  margin-left: 20px;
  margin-right: 20px;
}

.publication_img {
  margin-top: 40px;
  padding-top: 40px;
}

.magazine_content {
  margin: auto;
  display: block;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 10px;
  font-weight: 400;
}

.magazine_content a {
  text-decoration: none;
  color: #808080;
}

.magazine_content a:hover {
  font-size: 1.1em;
  color: #651fff;
  font-weight: 900;
}

.magazine_content .magazine_img {
  height: 500px;
}

.magazine_content .magazine_content__caption :a {
  margin-left: 20px;
  align-self: center;
}

.about_wrapper {
  margin-top: 30px;
  padding: 30px;
  background-color: #FED880;
  color: black;
}

.about__title {
  font-size: 1.5em;
}

.home_title {
  color: black;
  font-size: 1.8em;
  font-weight: 600;
  margin-top: 20px;
}

.text_one,
.text_two,
.text_three {
  margin-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  border-left: 1px solid;
  font-size: 1.1em;
  text-align: center;
  line-height: 1.8;
}

.text_three {
  border-right: 1px solid;
}

@media screen and (max-width: 600px) {
  .three-column, .two-column, .card {
    width: auto;
    grid-template-columns: none;
  }
}

.skills_wrapper {
  width: 50%;
}

.skills {
  margin-top: 5px;
  margin-bottom: 10px;
  margin-left: 10px;
  padding-top: 5px;
  padding-bottom: 15px;
  color: black;
}

.indesign,
.photoshop, .illustrator, .corel, .html-css, .javascript {
  height: 10px;
  text-align: right;
  color: black;
  font-weight: 600;
  padding-right: 10px;
  align-content: center;
  background-color: #FED880;
}

ul {
  list-style: none;
}

ul li::before {
  content: '\2022';
  color: #FED880;
  font-weight: 900;
  font-size: 2em;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

.indesign {
  width: 90%;
}

.photoshop {
  width: 70%;
}

.illustrator {
  width: 70%;
}

.corel {
  width: 50%;
}

.html-css {
  width: 40%;
}

.javascript {
  width: 40%;
}

.search-bar {
  width: 50%;
  margin: auto;
}

.search-bar input {
  font-size: 20px;
  height: 40px;
  width: 100%;
  border: 1px solid #E6E6E6;
  border-radius: 20px;
  color: #808080;
}

.search-bar input::placeholder {
  color: #CCCCCC;
}

.search-bar input:focus {
  outline: none;
}

.search-bar-grid {
  display: grid;
  grid-template-rows: [s] 1fr [e];
  grid-template-columns: 20px [s] 20px [icon-e] 1fr [e] 50px;
}

.search-bar-grid__icon {
  grid-row: s/e;
  grid-column: s/icon-e;
  font-size: 18px;
  z-index: 100;
  align-self: center;
}

.search-bar-grid__input {
  grid-row: s/e;
  grid-column: 1/e;
  padding-left: 50px;
}

.logos-wrapper {
  text-align: center;
  display: grid;
}

.card {
  margin: 10px;
  box-shadow: 0px 0px 3px 1px #ccc;
  padding: 10px;
  border-radius: 10px;
  width: 800px;
  height: 450px;
  text-align: center;
  background-color: #f6f6f6;
}

.card .left-side {
  margin: auto;
}

.card .right-side {
  margin: auto;
}

.card-image {
  width: 400px;
  padding: 10px;
  object-fit: cover;
}

.card-title {
  align-items: center;
  font-size: 24px;
  font-weight: 900;
}

.rec-carousel-wrapper {
  width: auto;
  height: fit-content;
  align-items: center;
  display: flex;
  column-gap: 15px;
}

.rec-carousel-wrapper .rec-carousel-item {
  height: 500px;
  object-fit: cover;
}

.rec-carousel-wrapper .rec-pagination {
  position: relative;
  padding-top: 5px;
}

.slides {
  width: 500px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
}
